.song-row {
  display: flex;
  overflow: hidden;
  white-space: nowrap;
  animation: scroll 600s linear infinite;
  -webkit-animation: scroll 600s linear infinite;
  clip-path: inset(0 0 0 0);
  width: calc(200%); /* Double width for loop */
  will-change: transform;
}
  
  .song-item {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    width: 180px; /* Adjust the size of the logo */
  }
  
  .song-logo {
    width: 150px; /* Size of the album logo */
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .song-name {
    font-size: 10px;
    text-align: center;
    margin-top: 5px;
    color: black; /* Adjust text color */
    font-weight: bold;
  }

  .song-item:hover {
    transform: scale(1.1);
    transition: transform 0.2s ease-in-out;
    cursor: pointer;
  }

  @keyframes scroll {
    0% {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    100% {
      -webkit-transform: translate3d(-50%, 0, 0);
      transform: translate3d(-50%, 0, 0);
    }
  }
  